body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
}

.sticky {
  position: sticky;
  top: 0;
  background: white;
  border-bottom: 2px lightgrey solid;
  padding: 10px 0px;
  /* background: lightgrey; */
}

.sticky > div {
  padding: 5px 0px;
  box-sizing: border-box;
  width: 100%;
}

.actions {
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.invisible {
  visibility: hidden;
}

.hidden {
  display: none;
}

.imgDock {
  border: 2px lightgray solid;
  text-align: left;
}

.imgDock img {
  border: 1px grey solid;
  margin: 5px 10px;
  width: 8%;
}

#cursorInfo {
  float: right;
  font-family: 'Courier New', Courier, monospace;
  font-size: 14px;
  margin: 5px;
}

#cursorSwatch {
  width: 10px;
  height: 10px;
  margin: 2px;
  border: 1px solid black;
  float: right;
}

canvas {
  /* border: 2px lightgray solid; */
  box-sizing: border-box;
  width: 100%;
}
